import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import { BrowserRouter as Router } from "react-router-dom";

import App from "./components/app";
import ErrorBoundry from "./components/error-boundry";
import DatastoreService from "./services/apistore-service";
import { DatastoreServiceProvider } from "./components/apistore-service-context";

import store from "./store";

const datastoreService = new DatastoreService();

ReactDOM.render(
  <Provider store={store}>
    <ErrorBoundry>
      <DatastoreServiceProvider value={datastoreService}>
        <Router>
          <App />
        </Router>
      </DatastoreServiceProvider>
    </ErrorBoundry>
  </Provider>,
  document.getElementById("root")
);
